/**************************************************/
/* Styling our project card when it isn't focused */
/**************************************************/

.hover {
    margin: 0 auto;
    width: 80vw;
    height: 15vh;
}

.projectCard {
    margin: 0 auto;
    width: 80vw;
    height: 15vh;
    background-color: gainsboro;
    border-radius: 15px;
    position: relative;
}

.projectCard img {
    width: 80vw;
    height: 15vh;
    border-radius: 15px;
}

.projectCard .blackLayer {
    background-color: black;
    width: 100%;
    height: 15vh;
    border-radius: 15px;
    position: absolute;
    opacity: 0.8;
}

.projectCard .texts {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;
}

.projectCard p.projectTitle {
    margin: 0;
    font-size: 25px;
    font-weight: bold;
}

.projectCard p.projectDescription {
    width: 80%;
    font-size: 15px;
    font-weight: 300;
    word-wrap: break-word;    
}

/**********************************************/
/* Styling our project card when it's focused */
/**********************************************/

.focus_projectCard {
    margin: 0 auto;
    width: 80vw;
    height: 65vh;
    background-color: var(--color-white-secondary);
    border-radius: 15px;
}

.focus_projectCard img {
    width: 100%;
    height: 25%;
    border-top-right-radius: 15px ;
    border-top-left-radius: 15px ;
}

.focus_projectCard .texts {
    width: 100%;
    height: 55%;
    display: flex;
    flex-direction: column;
    color: black;
    text-align: left;
    padding: 1rem 1rem 0 1rem;
}

.focus_projectCard p.projectTitle {
    font-size: 25px;
    font-weight: bold;
    margin-bottom: 10px;
}

.focus_projectCard p.projectDescription {
    height: 80%;
    font-size: 17px;
    font-weight: 400;
    word-wrap: break-word;
    margin-bottom: 0;
    text-align: justify;
}

.cardBottom {
    width: 100%;
    padding: 0rem 1rem 0 1rem;
    display: flex;
    flex-direction: column;    
}

.textsDivider {
    margin: 0px 0 8px 0;
    color: black;
    align-self: center;
    width: 100%;
    border: 0;
    height: 0;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}

.linksArea {
    margin-top: 10px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    column-gap: 6px;
}

.linksArea p {
    font-size: 15px;
    padding: 10px;
    transition: background 0.3s;
    border-radius: 10px;
    margin-left: -8px;
    text-decoration: none;
    font-weight: bold;
    color: var(--color-primary);
}

.linksArea p:hover {
    background-color: rgb(224, 224, 224);
}

@media (max-height: 600px){
    .projectTitle {
        font-size: 20px !important;
    }

    .projectDescription {
        font-size: 13px !important;
    }
}

@media (min-height: 601px) and (max-height: 700px){
    .projectTitle {
        font-size: 20px !important;
    }

    .projectDescription {
        font-size: 0.95em !important;
    }
}

@media (max-width: 300px){
    .projectTitle {
        font-size: 20px !important;
    }

    .projectDescription {
        font-size: 13px !important;
    }
}


@media (min-width: 540px) and (max-width: 1025px){
    .hover {
        width: 60vw;
        height: 17vh;
    }
    
    .projectCard {
        width: 60vw;
        height: 17vh;
    }
    
    .projectCard img {
        width: 60vw;
        height: 17vh;
    }
    
    .projectCard .blackLayer {
        width: 60vw;
        height: 17vh;
        cursor: pointer;
    }

}

@media (min-width: 1025px){

    /* card isnt focused */
    
    .hover {
        width: 40vw;
        height: 30vh;
    }
    
    .projectCard {
        width: 40vw;
        height: 30vh;
    }
    
    .projectCard img {
        width: 40vw;
        height: 30vh;
    }
    
    .projectCard .blackLayer {
        height: 30vh;
        cursor: pointer;
    }
    
    .projectTitle {
        font-size: 2rem !important;
    }

    .projectDescription {
        font-size: 1.3rem !important;
    }

    /* card is focused */

    .focus_projectCard {
        width: 45vw;
        height: 65vh;
    }
    
    .focus_projectCard img {
        width: 40%;
        height: 50%;
        border-top-right-radius: 0px ;
        border-top-left-radius: 0px ;
        border-radius: 10px;
        /* border: 8px solid black; */
        margin-left: 30%;
        position: absolute;
    }
    
    .focus_projectCard .texts {
        padding: 2rem 2rem 0 2rem;
    }
    
    .focus_projectCard p.projectTitle {
        font-size: 20px;
    }
    
    .textsDivider {
        margin: 20% 2rem 1px 2rem;
        /* align-self: left; */
        /* text-align: left; */
        width: 93%;
    }
    
    .linksArea {
        margin: 10px 0 0 1rem;
    }
}